import React, { Component } from 'react'

class Social extends Component {
    render(){
        return(
            <div className="social">
                <a href="https://codepen.io/" target="_blank"><i className="fab fa-codepen"></i></a>
                <a href="https://github.com/" target="_blank"><i className="fab fa-github"></i></a>
                <a href="https://Instagram.com/" target="_blank"><i className="fab fa-instagram"></i></a>
                <a href="https://www.facebook.com/" target="_blank"><i className="fab fa-facebook-f"></i></a> 
                <a href="https://Linkedin.com/in/" target="_blank"><i className="fab fa-linkedin-in"></i></a>
                <a href="https://medium.com/" target="_blank"><i className="fab fa-medium-m"></i></a>
            </div>
        )
    }
}

export default Social